@import url(https://fonts.googleapis.com/css2?family=Sora&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: montserrat, sans-serif;  
}

* {
  font-family: 'Sora', sans-serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.welcome h1{
  color: orange;
  font-size: 38px;
  font-weight: 500;
  margin-bottom:10px;
}

.btn{
    height: 40px;
    width: 120px;
    font-size: 20px;
    border-radius: 25px;
    background-color:  orange;
    border: none;
    margin-bottom:50px;
    color: blue;
    font-weight: bolder;
}
.video{
  width: 853px;
  height: 480px;
}


.App{
  position: relative;
  display: flex;
  flex-direction: column;
  text-align: center;
}

.log{
  display: flex;
  align-items: center;
  justify-content: center;
}

@media(max-width: 425px){

  .App{
    position: relative;
    display: flex;
    flex-direction: column;
    text-align: center;
    width: 100%;
    height: 600px;
  }

  .video{
    width: 353px;
    height: 250px;
  }
  
  .welcome h1{
    color: orange;
    font-size: 28px;
    font-weight: 500;
    margin-bottom:10px;
  }

  .player{
    width: 425px;
  }
  
  .btn{
      height: 40px;
      width: 120px;
      font-size: 20px;
      border-radius: 25px;
      background-color:  orange;
      border: none;
      margin-bottom:50px;
      color: blue;
      font-weight: bolder;
  }
  
  .log{
    display: flex;
    align-items: center;
    justify-content: center;
  }
}



.form-inner {
    width: 550px;
    height: 550px;
    border-radius: 20px;
    background-color: rgb(39, 26, 221);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.form-group{
    display: flex;
    flex-direction: column;
    text-align: center;
    height: 100px;
    color: orange;
}

.button{
    height: 40px;
    width: 100px;
    font-size: 20px;
    border-radius: 25px;
    background-color:  orange;
    border: none;
    color: blue;
    font-weight: bolder;
}

.input{
    width: 400px;
    height: 40px;
    font-size: 22px;
    border-radius: 10px;
    padding-left: 10px;
    border: none;
    margin-top: 10px;
    cursor: pointer;
 }

 h2{
    color: orange;
    font-size: 38px;
    font-weight: 500;
    margin-bottom:30px;
    text-transform: uppercase;
 }

 .form-group label {
    font-size: 20px;
 }

 @media(max-width: 425px){
    .LoginForm{
       margin: 0 auto;
       padding: 0 auto;
    }
    
    .form-inner {
        width: 350px;
        height: 350px;
        border-radius: 20px;
        background-color: rgb(39, 26, 221);
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .form-group{
        display: flex;
        flex-direction: column;
        text-align: center;
        height: 80px;
        color: orange;
    }

    .button{
        height: 30px;
        width: 100px;
        font-size: 20px;
        border-radius: 25px;
        background-color:  orange;
        border: none;
        color: blue;
        font-weight: bolder;
    }

    .input{
        width: 300px;
        height: 30px;
        font-size: 16px;
        border-radius: 10px;
        padding-left: 10px;
        border: none;
        margin-top: 10px;
        cursor: pointer;
    }

    h2{
        color: orange;
        font-size: 28px;
        font-weight: 500;
        margin-bottom:30px;
        text-transform: uppercase;
    }

    .form-group label {
        font-size: 16px;
    }
 }
.header {
    display: flex;
    justify-content: space-around;
    height: 200px;
    width: 100%;
    align-items: center;
 }
 
 img {
    width: 180px;
    height: 90px;
 }
 
 @media(max-width: 425px){
    .Header{
       margin: 0 auto;
       padding: 0 auto;
    }
    .header{
       height: 100px;
    }
    img{
       width: 80px;
       height: 35px;
    }
 }
